
.app{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  /*grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;*/
  margin: 0 auto;
  width: 85%;
  height: 60vh;
  grid-gap: 2.5px;   
  margin-top: 25px;
  
}
.app>div{
  border-radius: 5px;
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: orangered;
  cursor: pointer;
  font-weight: 100;
}
.acts{
  margin-top: 2rem;
  margin-right: 10rem;
  margin-left: 10rem;
  display: grid;
  grid-template-columns: repeat(14,1fr);
  grid-gap: 2.5px;
  height: 7.8vh;

}
.acts>div{
  border-radius: 5px;
  background-color:aqua;
  position: relative;
  display: inline-flex;
