.details{
    border-radius: 10px;
    padding: 15px 15px;
    width: 25vw;
    height: 50vh;
    z-index: 10000;
    position: absolute;
    transform: translate(-50%,-50%);
    left: 50%;
    top: 50%;
    background-color: rgb(139, 12, 139);
    color:rgb(247, 236, 201);
    opacity: .95;
    display: flex;
}
.button{
    position: absolute;
    right: 12px;
    top: 10px;
    font-weight: bold;
    font-size: 20px;
    cursor: pointer;
}
.children{
    align-self: center;
    
}
.backdrop{
    position: absolute;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.76);
    width: 150%;
    height: 150%;
    top: -40px;
    left: -40px;
    
}
